<template>
    <section class="block-accueil">
        <header>
            <div class="header header-image">
                <img :src="image.url" :alt="image.alt">
            </div>
            <div class="header header-time">
                <DigitalClock :displaySeconds="true"></DigitalClock>
            </div>
        </header>
        <article class="block-accueil__block-text">
            <div class="block-text__title">
                <p>{{title}}<span></span></p>
            </div>
            <p class="block-text__content" v-html="text">
            </p>
        </article>
    </section>    
</template>
<script>
import DigitalClock from 'vue-digital-clock';

export default {
    name: 'Accueil',
    components: {
        DigitalClock
    },
    data() {
        return {
            title: '',
            text: '',
            image: ''
        }
    },
    created() {
        fetch('https://alectei.alwaysdata.net/wordpressvue/wp-json/wp/v2/pages/27')
            .then((r) => r.json())
            .then((res) => {
                this.title = res.content.rendered;
                this.text = res.acf.contenu;
                this.image = res.acf.logo
            });
    }
}
</script>
<style lang="scss" scoped>
.block-accueil {
    width: 100%;

    header {
        display: flex;
        justify-content: space-between;
        height: auto;
        align-items: center;
        padding-top: 65px;

        .header-image {
            height: 35px;
            
            img {
                height: 100%;
            }
        }

        .header-time {
            font-family: 'Helvetica Roman';
            font-size: 18px;
            color: #ffffff;
        }
    }

    .block-text__title {
        position: relative;

        p {
            position: relative;
            display: flex;

            span {
                display: block;
                position: relative;
                background: url('~@/assets/victory.png') no-repeat center;
                height: 45px;
                width: 40px;
                background-size: 45px;
                margin-top: -4px;
                right: -5px;
            }
        }
        
    }

    .block-accueil__block-text {
        padding-top: 275px;
        font-family: 'Founders Regular';
        font-size: 48px;
        line-height: 60px;
    }
}
</style>
