<template>
    <section class="block-posts">
        <div class="block-posts__post-container" v-for="(post, index) in posts"
        :key=index>
            <article class="post-container__post">
                <div class="post-title">
                    <p class="post__title" v-html="post.title.rendered"></p>
                    <span>0{{index + 1}}</span>
                </div>
                <p class="post__category">{{post.acf.category}}</p>
                <p class="post__year">{{post.acf.year}}</p>
            </article>
            <div class="line-post" v-if="index >= 1"></div>
        </div>
    </section>  
</template>
<script>
export default {
    name: 'Posts',
    data() {
        return {
            posts: []
        }
    },
    created() {
        fetch('https://alectei.alwaysdata.net/wordpressvue/wp-json/wp/v2/posts')
            .then((r) => r.json())
            .then((response) => {
                console.log(response)
                this.posts = response
            })
    }
}
</script>
<style lang="scss" scoped>
.block-posts {
    margin-top: 380px;
    width: 100%;

    .block-posts__post-container {
        width: 100%;
        position: relative;

        .post-container__post {
            width: 100%;
            display: flex;
            align-items: center;
            height: auto;
            
            .post-title {
                display: flex;
                width: 45%;
                
                p {
                    font-family: 'Helvetica Roman';
                    font-size: 48px;
                }

                span {
                    font-family: 'Founders Regular';
                    font-size: 24px;
                    color: white;
                    margin-left: 10px;
                    margin-top: 6px;
                }
            }

            .post__category, .post__year {
                width: 25%;
                font-size: 24px;
            }
        }

        .line-post {
            width: 100%;
            background: white;
            position: absolute;
            top: 0;
            left: 0;
            height: 1px;
        }

        &:not(:first-child) {
            padding-top: 45px;
        }

        &:not(:last-child) {
            padding-bottom: 45px;
        }
    }
}
</style>
