<template>
    <section class="block-contact">
        <article class="block-contact__intro-container" v-html="contactText">
        </article>
        <article class="block-contact__infos">
            <div class="line"></div>
            <p v-for="(item, key, index) in contact"
            :key='index' v-html="item"></p>
        </article>
    </section>
</template>
<script>
export default {
    name: 'Contact',
    data() {
        return {
            contactText: '',
            contact: ''
        }
    },
    created() {
        fetch('https://alectei.alwaysdata.net/wordpressvue/wp-json/wp/v2/pages/29')
            .then((r) => r.json())
            .then((response) => {
                this.contactText = response.content.rendered;
                this.contact = response.acf
            });
    }
}
</script>
<style lang="scss" scoped>
.block-contact {
    margin-top: 380px;
    width: 100%;
    display: flex;
    justify-content: space-between;

    .block-contact__intro-container {
        flex: 1;
        font-size: 48px;
        line-height: 60px;
        position: relative;
    }
    
    .block-contact__infos {
        width: 50%;
        position: relative;
        padding-top: 35px;
        font-family: 'Founders Regular';
        font-size: 24px;
        line-height: 1.2;
        display: flex;
        justify-content: space-between;
        
        .line {
            width: 100%;
            height: 1px;    
            position: absolute;
            top: 0;
            left: 0;
            background: white;
        }

        p {
            span {
                color: red;
            }
        }
    }
}
</style>
